
div.div-type > div {
  display: inline-block;
  width: 8%;
  margin: 80px 20%;
  text-align: center;
  cursor: pointer;
  font-weight: 400;
}
img
{
  width: 100%;
}
div.div-service {
  margin: 30px auto;
}
div.div-service > div {
  display: inline-block;
  width: 70%;
  margin-top: 10px;
  vertical-align: middle;
}
div.div-service > div:first-child
{
  width: 8%;
  margin: 10px 5% 10px 10%;
  text-align: center;
  cursor: pointer;
  font-weight: 400;
}



