
div.nav-bar {
  display: block;
}
a
{
  color: #888888;
  line-height: 30px;
}
li:hover {
  background-color: #eeeeee;
}
li:hover > a
{
  color: #000000;
}
div.nav-bar > ul {
  height: 50px;
}
div.nav-bar > ul > li {
  float: left;
  line-height: 50px;
  width: 100px;
  text-align: center;
}
div.nav-bar > ul > li > ul {
  display: none;
  background-color: #ffffff;
  border: 1px solid #f0f0f0;
  border-radius: 5px;
  width: 100px;
  position: absolute;
}
div.nav-bar > ul >li >ul >li {
  background-color: #f4f4f4;
  text-align: center;
}
div.nav-bar > ul >li:hover > ul
{
  display: block;
}



